"use client"

import React, { memo } from 'react'
import {
  BaseEdge,
  EdgeProps,
  EdgeLabelRenderer,
  getBezierPath,
} from 'reactflow'
import { Percent, MoreHorizontal } from 'lucide-react'
import {
  DropdownMenu,
  DropdownMenuContent,
  DropdownMenuItem,
  DropdownMenuTrigger,
} from '../ui/dropdown-menu'
import { Edge } from '../../types/entities'

const OwnershipEdge = ({
  id,
  sourceX,
  sourceY,
  targetX,
  targetY,
  sourcePosition,
  targetPosition,
  data,
  selected,
}: EdgeProps<Edge['data']>) => {
  const [edgePath, labelX, labelY] = getBezierPath({
    sourceX,
    sourceY,
    sourcePosition,
    targetX,
    targetY,
    targetPosition,
  })

  const percentage = data?.percentage || 0
  const strokeWidth = Math.max(1, Math.min(percentage / 25, 3))

  return (
    <>
      {/* Animated Edge Path */}
      <path
        id={id}
        className={`react-flow__edge-path ${selected ? '!stroke-blue-500' : ''}`}
        d={edgePath}
        style={{
          strokeWidth,
          strokeDasharray: '5,5',
          animation: 'flow 30s linear infinite',
        }}
      />

      {/* Edge Label */}
      <EdgeLabelRenderer>
        <div
          style={{
            position: 'absolute',
            transform: `translate(-50%, -50%) translate(${labelX}px,${labelY}px)`,
            pointerEvents: 'all',
          }}
          className="group flex items-center gap-1"
        >
          <div
            className={`flex items-center gap-1 rounded-full bg-white px-2 py-1 text-xs font-medium shadow-sm transition-all
              ${selected ? 'ring-2 ring-blue-500' : ''}
              group-hover:shadow-md
            `}
          >
            <Percent className="h-3 w-3" />
            {percentage}%
          </div>

          {/* Quick Actions */}
          <div className="opacity-0 transition-opacity group-hover:opacity-100">
            <DropdownMenu>
              <DropdownMenuTrigger asChild>
                <button className="rounded-full bg-white p-1 shadow-sm hover:bg-gray-50">
                  <MoreHorizontal className="h-3 w-3" />
                </button>
              </DropdownMenuTrigger>
              <DropdownMenuContent>
                <DropdownMenuItem>Edit Percentage</DropdownMenuItem>
                <DropdownMenuItem className="text-red-600">
                  Remove Connection
                </DropdownMenuItem>
              </DropdownMenuContent>
            </DropdownMenu>
          </div>
        </div>
      </EdgeLabelRenderer>

      {/* CSS for Edge Animation */}
      <style jsx global>{`
        @keyframes flow {
          from {
            stroke-dashoffset: 100;
          }
          to {
            stroke-dashoffset: 0;
          }
        }
      `}</style>
    </>
  )
}

export default memo(OwnershipEdge)
