"use client"

import React, { useCallback, useRef, useEffect } from 'react'
import ReactFlow, {
  Background,
  Controls,
  MiniMap,
  Connection,
  useNodesState,
  useEdgesState,
  useReactFlow,
  Panel,
  NodeTypes,
  EdgeTypes,
  OnNodesChange,
  OnEdgesChange,
  NodeChange,
  OnSelectionChangeParams,
  Viewport,
  useKeyPress,
  Node,
  SelectionMode,
} from 'reactflow'
import 'reactflow/dist/style.css'

import useCanvasStore from '../../stores/canvasStore'
import useUIStore from '../../stores/uiStore'
import CompanyNode from '../Nodes/CompanyNode'
import TrustNode from '../Nodes/TrustNode'
import IndividualNode from '../Nodes/IndividualNode'
import OwnershipEdge from '../Edges/OwnershipEdge'
import GovernanceEdge from '../Edges/GovernanceEdge'
import { FlowNode } from '../../types/entities'

const nodeTypes: NodeTypes = {
  company: CompanyNode,
  trust: TrustNode,
  individual: IndividualNode,
}

const edgeTypes: EdgeTypes = {
  ownership: OwnershipEdge,
  governance: GovernanceEdge,
}

const MIN_ZOOM = 0.1
const MAX_ZOOM = 2
const VIEWPORT_BOUNDS = [-5000, -5000, 5000, 5000] as const
const AUTO_SAVE_INTERVAL = 30000 // 30 seconds

export default function FlowCanvas() {
  const reactFlowWrapper = useRef<HTMLDivElement>(null)
  const connectingNodeId = useRef<string | null>(null)
  const isShiftPressed = useKeyPress('Shift')

  const {
    nodes: storeNodes,
    edges: storeEdges,
    addNode,
    updateNodePosition,
    addEdge,
    setSelectedNodes,
    setSelectedEdges,
    setViewport,
    autoLayout,
  } = useCanvasStore()

  const setEntityDialogOpen = useUIStore((state) => state.setEntityDialogOpen)

  const [nodes, setNodes, onNodesChange] = useNodesState([])
  const [edges, setEdges, onEdgesChange] = useEdgesState([])
  const { project, getViewport } = useReactFlow()

  useEffect(() => {
    const flowNodes = storeNodes.map((node) => ({
      id: node.id,
      type: node.type,
      data: node,
      position: node.position,
      draggable: true,
      selectable: true,
    }))
    setNodes(flowNodes as any)
    setEdges(storeEdges)
  }, [storeNodes, storeEdges, setNodes, setEdges])

  useEffect(() => {
    const interval = setInterval(() => {
      console.log('Auto-saving canvas state...')
    }, AUTO_SAVE_INTERVAL)
    return () => clearInterval(interval)
  }, [])

  const onConnect = useCallback(
    (params: Connection) => {
      if (params.source && params.target) {
        addEdge({
          id: `${params.source}-${params.target}`,
          source: params.source,
          target: params.target,
          type: 'ownership',
          data: { percentage: 100 },
        })
      }
    },
    [addEdge]
  )

  const handleNodeChange = useCallback(
    (changes: NodeChange[]) => {
      onNodesChange(changes)
      changes.forEach((change) => {
        if (change.type === 'position' && change.position && change.id) {
          updateNodePosition(change.id, change.position)
        }
      })
    },
    [onNodesChange, updateNodePosition]
  )

  const onSelectionChange = useCallback(
    (params: OnSelectionChangeParams) => {
      setSelectedNodes(params.nodes.map((n) => n.id))
      setSelectedEdges(params.edges.map((e) => e.id))
    },
    [setSelectedNodes, setSelectedEdges]
  )

  const onPaneClick = useCallback(() => {
    setSelectedNodes([])
    setSelectedEdges([])
  }, [setSelectedNodes, setSelectedEdges])

  const onDoubleClick = useCallback(
    (event: React.MouseEvent<Element>) => {
      event.preventDefault()
      const bounds = reactFlowWrapper.current?.getBoundingClientRect()
      const position = project({
        x: event.clientX - (bounds?.left ?? 0),
        y: event.clientY - (bounds?.top ?? 0),
      })
      setEntityDialogOpen(true)
    },
    [project, setEntityDialogOpen]
  )

  const onPaneContextMenu = useCallback(
    (event: React.MouseEvent<Element>) => {
      event.preventDefault()
      const bounds = reactFlowWrapper.current?.getBoundingClientRect()
      const position = project({
        x: event.clientX - (bounds?.left ?? 0),
        y: event.clientY - (bounds?.top ?? 0),
      })
      addNode({
        id: `node-${Date.now()}`,
        type: 'company',
        name: 'New Company',
        position,
      })
    },
    [project, addNode]
  )

  // Sync viewport with store
  useEffect(() => {
    const viewport = getViewport()
    if (viewport) {
      setViewport(viewport)
    }
  }, [getViewport, setViewport])

  return (
    <div ref={reactFlowWrapper} className="w-full h-full">
      <ReactFlow
        nodes={nodes}
        edges={edges}
        onNodesChange={handleNodeChange}
        onEdgesChange={onEdgesChange}
        onConnect={onConnect}
        onSelectionChange={onSelectionChange}
        onPaneClick={onPaneClick}
        onDoubleClick={onDoubleClick}
        onPaneContextMenu={onPaneContextMenu}
        nodeTypes={nodeTypes}
        edgeTypes={edgeTypes}
        minZoom={MIN_ZOOM}
        maxZoom={MAX_ZOOM}
        fitView
        snapToGrid
        snapGrid={[20, 20]}
        defaultViewport={{ x: 0, y: 0, zoom: 1 }}
        selectionMode={isShiftPressed ? SelectionMode.Partial : SelectionMode.Full}
        selectNodesOnDrag={false}
      >
        <Background gap={20} color="#e5e7eb" />
        <Controls />
        <MiniMap
          nodeColor={(node) => {
            switch (node.type) {
              case 'company':
                return '#3b82f6'
              case 'trust':
                return '#8b5cf6'
              case 'individual':
                return '#10b981'
              default:
                return '#64748b'
            }
          }}
          maskColor="rgba(248, 250, 252, 0.8)"
        />
        <Panel position="top-right" className="bg-white p-2 rounded-lg shadow-lg">
          <div className="text-sm text-gray-500">
            Double-click to add a new entity
          </div>
        </Panel>
      </ReactFlow>
    </div>
  )
}
