import { create } from 'zustand'
import { devtools } from 'zustand/middleware'
import { Entity, CompanyEntity, TrustEntity, IndividualEntity, Document, ComplianceStatus } from '@/types/entities'

type EntityMap = Record<string, Entity>
type DocumentMap = Record<string, Document>
type LoadingMap = Record<string, boolean>

interface EntityState {
  entities: EntityMap
  documents: DocumentMap
  entityLoading: LoadingMap
  documentLoading: LoadingMap
  selectedNodes: string[]
  error: string | null

  // Entity Actions
  addEntity: (entity: Entity) => void
  updateEntity: <T extends Entity>(id: string, data: Partial<T>) => void
  deleteEntity: (id: string) => void
  getEntity: (id: string) => Entity | undefined
  setSelectedNodes: (ids: string[]) => void
  
  // Company-specific Actions
  updateCompanyCompliance: (id: string, status: ComplianceStatus) => void
  updateCompanyStatus: (id: string, status: CompanyEntity['status']) => void
  
  // Trust-specific Actions
  addTrustee: (trustId: string, trusteeId: string) => void
  removeTrustee: (trustId: string, trusteeId: string) => void
  addBeneficiary: (trustId: string, beneficiaryId: string) => void
  removeBeneficiary: (trustId: string, beneficiaryId: string) => void
  
  // Individual-specific Actions
  updateShareholding: (
    individualId: string,
    entityId: string,
    percentage: number
  ) => void
  removeShareholding: (individualId: string, entityId: string) => void
  
  // Document Actions
  addDocument: (document: Document) => void
  updateDocument: (id: string, data: Partial<Document>) => void
  deleteDocument: (id: string) => void
  getEntityDocuments: (entityId: string) => Document[]
  
  // Loading & Error States
  setEntityLoading: (id: string, loading: boolean) => void
  setDocumentLoading: (id: string, loading: boolean) => void
  setError: (error: string | null) => void
}

const useEntityStore = create<EntityState>()(
  devtools(
    (set, get) => ({
      entities: {},
      documents: {},
      entityLoading: {},
      documentLoading: {},
      selectedNodes: [],
      error: null,

      // Entity Actions
      addEntity: (entity) =>
        set((state) => ({
          entities: { ...state.entities, [entity.id]: entity },
        })),

      updateEntity: (id, data) =>
        set((state) => {
          const entity = state.entities[id]
          if (!entity) return state

          return {
            entities: {
              ...state.entities,
              [id]: { ...entity, ...data },
            },
          }
        }),

      deleteEntity: (id) =>
        set((state) => {
          const { [id]: _, ...remainingEntities } = state.entities
          const remainingDocuments = Object.entries(state.documents).reduce(
            (acc, [docId, doc]) => {
              if (doc.entityId !== id) {
                acc[docId] = doc
              }
              return acc
            },
            {} as DocumentMap
          )
          return {
            ...state,
            entities: remainingEntities,
            documents: remainingDocuments,
          }
        }),

      getEntity: (id) => get().entities[id],

      setSelectedNodes: (ids) =>
        set({ selectedNodes: ids }),

      // Company-specific Actions
      updateCompanyCompliance: (id, status) =>
        set((state) => {
          const entity = state.entities[id]
          if (!entity || entity.type !== 'company') return state

          return {
            entities: {
              ...state.entities,
              [id]: {
                ...entity,
                complianceStatus: status,
              },
            },
          }
        }),

      updateCompanyStatus: (id, status) =>
        set((state) => {
          const entity = state.entities[id]
          if (!entity || entity.type !== 'company') return state

          return {
            entities: {
              ...state.entities,
              [id]: {
                ...entity,
                status,
              },
            },
          }
        }),

      // Trust-specific Actions
      addTrustee: (trustId, trusteeId) =>
        set((state) => {
          const trust = state.entities[trustId]
          if (!trust || trust.type !== 'trust') return state

          return {
            entities: {
              ...state.entities,
              [trustId]: {
                ...trust,
                trustees: [...new Set([...trust.trustees, trusteeId])],
              },
            },
          }
        }),

      removeTrustee: (trustId, trusteeId) =>
        set((state) => {
          const trust = state.entities[trustId]
          if (!trust || trust.type !== 'trust') return state

          return {
            entities: {
              ...state.entities,
              [trustId]: {
                ...trust,
                trustees: trust.trustees.filter((id) => id !== trusteeId),
              },
            },
          }
        }),

      addBeneficiary: (trustId, beneficiaryId) =>
        set((state) => {
          const trust = state.entities[trustId]
          if (!trust || trust.type !== 'trust') return state

          return {
            entities: {
              ...state.entities,
              [trustId]: {
                ...trust,
                beneficiaries: [...new Set([...trust.beneficiaries, beneficiaryId])],
              },
            },
          }
        }),

      removeBeneficiary: (trustId, beneficiaryId) =>
        set((state) => {
          const trust = state.entities[trustId]
          if (!trust || trust.type !== 'trust') return state

          return {
            entities: {
              ...state.entities,
              [trustId]: {
                ...trust,
                beneficiaries: trust.beneficiaries.filter((id) => id !== beneficiaryId),
              },
            },
          }
        }),

      // Individual-specific Actions
      updateShareholding: (individualId, entityId, percentage) =>
        set((state) => {
          const individual = state.entities[individualId]
          if (!individual || individual.type !== 'individual') return state

          const shareholdings = [...individual.shareholdings]
          const existingIndex = shareholdings.findIndex((s) => s.entityId === entityId)

          if (existingIndex >= 0) {
            shareholdings[existingIndex] = { ...shareholdings[existingIndex], percentage }
          } else {
            shareholdings.push({ entityId, percentage })
          }

          return {
            entities: {
              ...state.entities,
              [individualId]: {
                ...individual,
                shareholdings,
              },
            },
          }
        }),

      removeShareholding: (individualId, entityId) =>
        set((state) => {
          const individual = state.entities[individualId]
          if (!individual || individual.type !== 'individual') return state

          return {
            entities: {
              ...state.entities,
              [individualId]: {
                ...individual,
                shareholdings: individual.shareholdings.filter((s) => s.entityId !== entityId),
              },
            },
          }
        }),

      // Document Actions
      addDocument: (document) =>
        set((state) => ({
          documents: { ...state.documents, [document.id]: document },
        })),

      updateDocument: (id, data) =>
        set((state) => ({
          documents: {
            ...state.documents,
            [id]: { ...state.documents[id], ...data },
          },
        })),

      deleteDocument: (id) =>
        set((state) => {
          const { [id]: _, ...remainingDocuments } = state.documents
          return { ...state, documents: remainingDocuments }
        }),

      getEntityDocuments: (entityId) =>
        Object.values(get().documents).filter((doc) => doc.entityId === entityId),

      // Loading & Error States
      setEntityLoading: (id, loading) =>
        set((state) => ({
          entityLoading: { ...state.entityLoading, [id]: loading },
        })),

      setDocumentLoading: (id, loading) =>
        set((state) => ({
          documentLoading: { ...state.documentLoading, [id]: loading },
        })),

      setError: (error) => set({ error }),
    }),
    {
      name: 'entity-store',
    }
  )
)

export default useEntityStore
