"use client"

import React from 'react'
import { ScrollArea } from '@/components/ui/scroll-area'
import { Tabs, TabsContent, TabsList, TabsTrigger } from '@/components/ui/tabs'
import {
  Card,
  CardContent,
  CardDescription,
  CardHeader,
  CardTitle,
} from '@/components/ui/card'
import { Button } from '@/components/ui/button'
import { Input } from '@/components/ui/input'
import { Label } from '@/components/ui/label'
import {
  Select,
  SelectContent,
  SelectItem,
  SelectTrigger,
  SelectValue,
} from '@/components/ui/select'
import { FileUp, Clock, MessageSquare } from 'lucide-react'
import useEntityStore from '@/stores/entityStore'
import useUIStore from '@/stores/uiStore'
import { CompanyEntity, TrustEntity, IndividualEntity } from '@/types/entities'

export default function RightPanel() {
  const selectedNodes = useEntityStore((state) => state.selectedNodes)
  const entities = useEntityStore((state) => state.entities)
  const documents = useEntityStore((state) => state.documents)
  const isRightPanelOpen = useUIStore((state) => state.isRightPanelOpen)
  const activePanel = useUIStore((state) => state.activePanel)
  const setActivePanel = useUIStore((state) => state.setActivePanel)

  const selectedEntity = selectedNodes.length === 1 
    ? entities[selectedNodes[0]]
    : null

  if (!isRightPanelOpen || !selectedEntity) {
    return null
  }

  const renderEntityDetails = () => {
    switch (selectedEntity.type) {
      case 'company':
        return <CompanyDetails entity={selectedEntity as CompanyEntity} />
      case 'trust':
        return <TrustDetails entity={selectedEntity as TrustEntity} />
      case 'individual':
        return <IndividualDetails entity={selectedEntity as IndividualEntity} />
      default:
        return null
    }
  }

  return (
    <div className="flex h-full w-80 flex-col border-l bg-white">
      <Tabs
        defaultValue="details"
        value={activePanel || 'details'}
        onValueChange={(value) => setActivePanel(value as any)}
        className="h-full"
      >
        <div className="border-b px-4 pt-4">
          <TabsList className="w-full">
            <TabsTrigger value="details" className="flex-1">
              Details
            </TabsTrigger>
            <TabsTrigger value="documents" className="flex-1">
              Documents
            </TabsTrigger>
            <TabsTrigger value="timeline" className="flex-1">
              Timeline
            </TabsTrigger>
          </TabsList>
        </div>

        <ScrollArea className="flex-1">
          <TabsContent value="details" className="m-0 p-4">
            {renderEntityDetails()}
          </TabsContent>

          <TabsContent value="documents" className="m-0 p-4">
            <DocumentsTab entityId={selectedEntity.id} />
          </TabsContent>

          <TabsContent value="timeline" className="m-0 p-4">
            <TimelineTab entityId={selectedEntity.id} />
          </TabsContent>
        </ScrollArea>
      </Tabs>
    </div>
  )
}

function CompanyDetails({ entity }: { entity: CompanyEntity }) {
  return (
    <Card>
      <CardHeader>
        <CardTitle>{entity.name}</CardTitle>
        <CardDescription>Company Details</CardDescription>
      </CardHeader>
      <CardContent className="space-y-4">
        <div className="space-y-2">
          <Label>Company Type</Label>
          <Select defaultValue={entity.companyType}>
            <SelectTrigger>
              <SelectValue />
            </SelectTrigger>
            <SelectContent>
              <SelectItem value="LLC">LLC</SelectItem>
              <SelectItem value="Corporation">Corporation</SelectItem>
              <SelectItem value="Partnership">Partnership</SelectItem>
              <SelectItem value="Other">Other</SelectItem>
            </SelectContent>
          </Select>
        </div>

        <div className="space-y-2">
          <Label>Registration Number</Label>
          <Input value={entity.registrationNumber} />
        </div>

        <div className="space-y-2">
          <Label>Jurisdiction</Label>
          <Input value={entity.jurisdiction} />
        </div>

        <div className="space-y-2">
          <Label>Status</Label>
          <Select defaultValue={entity.status}>
            <SelectTrigger>
              <SelectValue />
            </SelectTrigger>
            <SelectContent>
              <SelectItem value="Active">Active</SelectItem>
              <SelectItem value="Inactive">Inactive</SelectItem>
              <SelectItem value="Dissolved">Dissolved</SelectItem>
            </SelectContent>
          </Select>
        </div>
      </CardContent>
    </Card>
  )
}

function TrustDetails({ entity }: { entity: TrustEntity }) {
  return (
    <Card>
      <CardHeader>
        <CardTitle>{entity.name}</CardTitle>
        <CardDescription>Trust Details</CardDescription>
      </CardHeader>
      <CardContent className="space-y-4">
        <div className="space-y-2">
          <Label>Trust Type</Label>
          <Select defaultValue={entity.trustType}>
            <SelectTrigger>
              <SelectValue />
            </SelectTrigger>
            <SelectContent>
              <SelectItem value="Revocable">Revocable</SelectItem>
              <SelectItem value="Irrevocable">Irrevocable</SelectItem>
              <SelectItem value="Charitable">Charitable</SelectItem>
              <SelectItem value="Other">Other</SelectItem>
            </SelectContent>
          </Select>
        </div>

        <div className="space-y-2">
          <Label>Jurisdiction</Label>
          <Input value={entity.jurisdiction} />
        </div>

        <div className="space-y-2">
          <Label>Trustees ({entity.trustees.length})</Label>
          <div className="rounded-md border p-2">
            {entity.trustees.length === 0 ? (
              <p className="text-sm text-gray-500">No trustees added</p>
            ) : (
              <div className="space-y-1">
                {entity.trustees.map((trusteeId) => (
                  <div key={trusteeId} className="text-sm">
                    {trusteeId}
                  </div>
                ))}
              </div>
            )}
          </div>
        </div>
      </CardContent>
    </Card>
  )
}

function IndividualDetails({ entity }: { entity: IndividualEntity }) {
  return (
    <Card>
      <CardHeader>
        <CardTitle>{entity.name}</CardTitle>
        <CardDescription>Individual Details</CardDescription>
      </CardHeader>
      <CardContent className="space-y-4">
        <div className="space-y-2">
          <Label>Email</Label>
          <Input value={entity.email} />
        </div>

        <div className="space-y-2">
          <Label>Nationality</Label>
          <Input value={entity.nationality} />
        </div>

        <div className="space-y-2">
          <Label>Roles</Label>
          <div className="flex flex-wrap gap-1">
            {entity.role.map((role) => (
              <div
                key={role}
                className="rounded-full bg-gray-100 px-2 py-1 text-xs"
              >
                {role}
              </div>
            ))}
          </div>
        </div>

        <div className="space-y-2">
          <Label>Shareholdings</Label>
          <div className="rounded-md border p-2">
            {entity.shareholdings.length === 0 ? (
              <p className="text-sm text-gray-500">No shareholdings</p>
            ) : (
              <div className="space-y-1">
                {entity.shareholdings.map((holding) => (
                  <div
                    key={holding.entityId}
                    className="flex items-center justify-between text-sm"
                  >
                    <span>{holding.entityId}</span>
                    <span>{holding.percentage}%</span>
                  </div>
                ))}
              </div>
            )}
          </div>
        </div>
      </CardContent>
    </Card>
  )
}

function DocumentsTab({ entityId }: { entityId: string }) {
  const documents = useEntityStore((state) => 
    Object.values(state.documents).filter((doc) => doc.entityId === entityId)
  )

  return (
    <div className="space-y-4">
      <div className="flex justify-between">
        <h3 className="text-lg font-semibold">Documents</h3>
        <Button size="sm">
          <FileUp className="mr-2 h-4 w-4" />
          Upload
        </Button>
      </div>

      {documents.length === 0 ? (
        <div className="rounded-lg border-2 border-dashed p-8 text-center">
          <p className="text-sm text-gray-500">
            No documents uploaded yet
          </p>
        </div>
      ) : (
        <div className="space-y-2">
          {documents.map((doc) => (
            <Card key={doc.id}>
              <CardContent className="flex items-center justify-between p-4">
                <div>
                  <p className="font-medium">{doc.name}</p>
                  <p className="text-sm text-gray-500">{doc.type}</p>
                </div>
                <Button variant="ghost" size="sm">
                  View
                </Button>
              </CardContent>
            </Card>
          ))}
        </div>
      )}
    </div>
  )
}

function TimelineTab({ entityId }: { entityId: string }) {
  return (
    <div className="space-y-4">
      <div className="flex items-center justify-between">
        <h3 className="text-lg font-semibold">Timeline</h3>
        <Button variant="outline" size="sm">
          <Clock className="mr-2 h-4 w-4" />
          Filter
        </Button>
      </div>

      <div className="space-y-4">
        <div className="flex gap-4 border-l-2 border-gray-200 pl-4">
          <div className="flex-1">
            <p className="text-sm font-medium">Entity Created</p>
            <p className="text-xs text-gray-500">2 days ago</p>
            <p className="mt-1 text-sm text-gray-600">
              Initial entity setup completed
            </p>
          </div>
        </div>

        <div className="flex gap-4 border-l-2 border-gray-200 pl-4">
          <div className="flex-1">
            <p className="text-sm font-medium">Document Added</p>
            <p className="text-xs text-gray-500">1 day ago</p>
            <p className="mt-1 text-sm text-gray-600">
              Registration certificate uploaded
            </p>
          </div>
        </div>
      </div>
    </div>
  )
}
