import { create } from 'zustand'
import { devtools } from 'zustand/middleware'

interface UIState {
  isSidebarOpen: boolean
  isRightPanelOpen: boolean
  activePanel: 'details' | 'documents' | 'timeline' | null
  isEntityDialogOpen: boolean
  isDocumentViewerOpen: boolean
  searchQuery: string
  selectedDocumentId: string | null
  theme: 'light' | 'dark'
  notifications: Array<{
    id: string
    type: 'info' | 'success' | 'warning' | 'error'
    message: string
  }>

  // Actions
  toggleSidebar: () => void
  toggleRightPanel: () => void
  setActivePanel: (panel: UIState['activePanel']) => void
  setEntityDialogOpen: (isOpen: boolean) => void
  setDocumentViewerOpen: (isOpen: boolean) => void
  setSearchQuery: (query: string) => void
  setSelectedDocument: (documentId: string | null) => void
  setTheme: (theme: UIState['theme']) => void
  addNotification: (type: 'info' | 'success' | 'warning' | 'error', message: string) => void
  removeNotification: (id: string) => void
  clearNotifications: () => void
}

const useUIStore = create<UIState>()(
  devtools(
    (set) => ({
      isSidebarOpen: true,
      isRightPanelOpen: true,
      activePanel: null,
      isEntityDialogOpen: false,
      isDocumentViewerOpen: false,
      searchQuery: '',
      selectedDocumentId: null,
      theme: 'light',
      notifications: [],

      toggleSidebar: () =>
        set((state) => ({ isSidebarOpen: !state.isSidebarOpen })),

      toggleRightPanel: () =>
        set((state) => ({ isRightPanelOpen: !state.isRightPanelOpen })),

      setActivePanel: (panel) =>
        set({ activePanel: panel }),

      setEntityDialogOpen: (isOpen) =>
        set({ isEntityDialogOpen: isOpen }),

      setDocumentViewerOpen: (isOpen) =>
        set({ isDocumentViewerOpen: isOpen }),

      setSearchQuery: (query) =>
        set({ searchQuery: query }),

      setSelectedDocument: (documentId) =>
        set({ selectedDocumentId: documentId }),

      setTheme: (theme) =>
        set({ theme }),

      addNotification: (type, message) =>
        set((state) => ({
          notifications: [
            ...state.notifications,
            {
              id: Date.now().toString(),
              type,
              message,
            },
          ],
        })),

      removeNotification: (id) =>
        set((state) => ({
          notifications: state.notifications.filter((n) => n.id !== id),
        })),

      clearNotifications: () =>
        set({ notifications: [] }),
    }),
    {
      name: 'ui-store',
    }
  )
)

export default useUIStore
