import { create } from 'zustand'
import { devtools } from 'zustand/middleware'
import { Edge, Entity, Position, Viewport } from '@/types/entities'

interface CanvasState {
  nodes: Array<Entity & { position: Position }>
  edges: Edge[]
  selectedNodes: string[]
  selectedEdges: string[]
  viewport: Viewport
  isDragging: boolean
  isConnecting: boolean
  connectionStartNode: string | null
  
  // Actions
  addNode: (node: Entity, position: Position) => void
  updateNode: (id: string, data: Partial<Entity>) => void
  updateNodePosition: (id: string, position: Position) => void
  deleteNode: (id: string) => void
  addEdge: (edge: Edge) => void
  updateEdge: (id: string, data: Partial<Edge['data']>) => void
  deleteEdge: (id: string) => void
  setViewport: (viewport: Viewport) => void
  setSelectedNodes: (ids: string[]) => void
  setSelectedEdges: (ids: string[]) => void
  setDragging: (isDragging: boolean) => void
  setConnecting: (isConnecting: boolean) => void
  setConnectionStartNode: (nodeId: string | null) => void
  autoLayout: () => void
  clearSelection: () => void
  deleteSelected: () => void
}

const useCanvasStore = create<CanvasState>()(
  devtools(
    (set, get) => ({
      nodes: [],
      edges: [],
      selectedNodes: [],
      selectedEdges: [],
      viewport: { x: 0, y: 0, zoom: 1 },
      isDragging: false,
      isConnecting: false,
      connectionStartNode: null,

      addNode: (node, position) =>
        set((state) => ({
          nodes: [...state.nodes, { ...node, position }],
        })),

      updateNode: (id, data) =>
        set((state) => ({
          nodes: state.nodes.map((node) =>
            node.id === id ? { ...node, ...data } : node
          ),
        })),

      updateNodePosition: (id, position) =>
        set((state) => ({
          nodes: state.nodes.map((node) =>
            node.id === id ? { ...node, position } : node
          ),
        })),

      deleteNode: (id) =>
        set((state) => ({
          nodes: state.nodes.filter((node) => node.id !== id),
          edges: state.edges.filter(
            (edge) => edge.source !== id && edge.target !== id
          ),
        })),

      addEdge: (edge) =>
        set((state) => ({
          edges: [...state.edges, edge],
        })),

      updateEdge: (id, data) =>
        set((state) => ({
          edges: state.edges.map((edge) =>
            edge.id === id ? { ...edge, data: { ...edge.data, ...data } } : edge
          ),
        })),

      deleteEdge: (id) =>
        set((state) => ({
          edges: state.edges.filter((edge) => edge.id !== id),
        })),

      setViewport: (viewport) =>
        set({
          viewport,
        }),

      setSelectedNodes: (ids) =>
        set({
          selectedNodes: ids,
        }),

      setSelectedEdges: (ids) =>
        set({
          selectedEdges: ids,
        }),

      setDragging: (isDragging) =>
        set({
          isDragging,
        }),

      setConnecting: (isConnecting) =>
        set({
          isConnecting,
        }),

      setConnectionStartNode: (nodeId) =>
        set({
          connectionStartNode: nodeId,
        }),

      autoLayout: () => {
        // Implement hierarchical layout algorithm
        // This is a placeholder for now
        console.log('Auto layout not implemented yet')
      },

      clearSelection: () =>
        set({
          selectedNodes: [],
          selectedEdges: [],
        }),

      deleteSelected: () => {
        const { selectedNodes, selectedEdges } = get()
        set((state) => ({
          nodes: state.nodes.filter((node) => !selectedNodes.includes(node.id)),
          edges: state.edges.filter(
            (edge) =>
              !selectedEdges.includes(edge.id) &&
              !selectedNodes.includes(edge.source) &&
              !selectedNodes.includes(edge.target)
          ),
          selectedNodes: [],
          selectedEdges: [],
        }))
      },
    }),
    {
      name: 'canvas-store',
    }
  )
)

export default useCanvasStore
