"use client"

import React, { memo } from 'react'
import {
  EdgeProps,
  EdgeLabelRenderer,
  getSmoothStepPath,
} from 'reactflow'
import { Briefcase, MoreHorizontal } from 'lucide-react'
import {
  DropdownMenu,
  DropdownMenuContent,
  DropdownMenuItem,
  DropdownMenuTrigger,
} from '../ui/dropdown-menu'
import { Edge } from '../../types/entities'

const GovernanceEdge = ({
  id,
  sourceX,
  sourceY,
  targetX,
  targetY,
  sourcePosition,
  targetPosition,
  data,
  selected,
}: EdgeProps<Edge['data']>) => {
  const [edgePath, labelX, labelY] = getSmoothStepPath({
    sourceX,
    sourceY,
    sourcePosition,
    targetX,
    targetY,
    targetPosition,
    borderRadius: 8,
  })

  const role = data?.role || 'Director'

  return (
    <>
      {/* Edge Path */}
      <path
        id={id}
        className={`react-flow__edge-path ${selected ? '!stroke-blue-500' : ''}`}
        d={edgePath}
        style={{
          stroke: '#64748b',
          strokeWidth: 2,
        }}
      />

      {/* Edge Label */}
      <EdgeLabelRenderer>
        <div
          style={{
            position: 'absolute',
            transform: `translate(-50%, -50%) translate(${labelX}px,${labelY}px)`,
            pointerEvents: 'all',
          }}
          className="group flex items-center gap-1"
        >
          <div
            className={`flex items-center gap-1 rounded-full bg-white px-2 py-1 text-xs font-medium shadow-sm transition-all
              ${selected ? 'ring-2 ring-blue-500' : ''}
              group-hover:shadow-md
            `}
          >
            <Briefcase className="h-3 w-3 text-gray-500" />
            {role}
          </div>

          {/* Quick Actions */}
          <div className="opacity-0 transition-opacity group-hover:opacity-100">
            <DropdownMenu>
              <DropdownMenuTrigger asChild>
                <button className="rounded-full bg-white p-1 shadow-sm hover:bg-gray-50">
                  <MoreHorizontal className="h-3 w-3" />
                </button>
              </DropdownMenuTrigger>
              <DropdownMenuContent>
                <DropdownMenuItem>Change Role</DropdownMenuItem>
                <DropdownMenuItem>Add Additional Role</DropdownMenuItem>
                <DropdownMenuItem className="text-red-600">
                  Remove Connection
                </DropdownMenuItem>
              </DropdownMenuContent>
            </DropdownMenu>
          </div>
        </div>
      </EdgeLabelRenderer>

      {/* Arrow Markers */}
      <defs>
        <marker
          id={`governance-arrow-${id}`}
          markerWidth="12"
          markerHeight="12"
          refX="6"
          refY="6"
          orient="auto"
        >
          <path
            d="M2,2 L10,6 L2,10 L6,6 L2,2"
            style={{
              fill: selected ? '#3b82f6' : '#64748b',
              strokeWidth: 1,
            }}
          />
        </marker>
      </defs>

      {/* Edge Path with Arrow */}
      <path
        d={edgePath}
        style={{
          stroke: 'none',
          fill: 'none',
          markerEnd: `url(#governance-arrow-${id})`,
        }}
      />
    </>
  )
}

export default memo(GovernanceEdge)
