"use client"

import React, { memo } from 'react'
import { Handle, Position, NodeProps } from 'reactflow'
import { Box, Users, ChevronDown, MoreHorizontal } from 'lucide-react'
import { TrustEntity } from '../../types/entities'
import {
  DropdownMenu,
  DropdownMenuContent,
  DropdownMenuItem,
  DropdownMenuTrigger,
} from '../ui/dropdown-menu'
import useUIStore from '../../stores/uiStore'

const TrustNode = ({ data, selected }: NodeProps<TrustEntity>) => {
  const setEntityDialogOpen = useUIStore((state) => state.setEntityDialogOpen)
  const [isExpanded, setIsExpanded] = React.useState(false)

  return (
    <div
      className={`group relative rounded-lg bg-gradient-to-br from-purple-50 to-white shadow-lg transition-all
        ${selected ? 'ring-2 ring-purple-500' : ''}
        ${isExpanded ? 'w-80' : 'w-60'}
      `}
    >
      {/* Connection Handles */}
      <Handle
        type="target"
        position={Position.Top}
        className="!bg-purple-400 hover:!bg-purple-500"
      />
      <Handle
        type="source"
        position={Position.Bottom}
        className="!bg-purple-400 hover:!bg-purple-500"
      />
      <Handle
        type="target"
        position={Position.Left}
        className="!bg-purple-400 hover:!bg-purple-500"
      />
      <Handle
        type="source"
        position={Position.Right}
        className="!bg-purple-400 hover:!bg-purple-500"
      />

      {/* Header */}
      <div className="flex items-center justify-between border-b p-3">
        <div className="flex items-center gap-2">
          <div className="rounded-lg bg-purple-50 p-2">
            <Box className="h-5 w-5 text-purple-500" />
          </div>
          <div>
            <h3 className="font-medium">{data.name}</h3>
            <p className="text-xs text-gray-500">{data.trustType}</p>
          </div>
        </div>
        <div className="flex items-center gap-1">
          <div className="flex items-center rounded-full bg-purple-100 px-2 py-0.5">
            <Users className="mr-1 h-3 w-3 text-purple-500" />
            <span className="text-xs font-medium text-purple-700">
              {data.trustees.length}
            </span>
          </div>
          <DropdownMenu>
            <DropdownMenuTrigger asChild>
              <button className="rounded p-1 hover:bg-purple-50">
                <MoreHorizontal className="h-4 w-4" />
              </button>
            </DropdownMenuTrigger>
            <DropdownMenuContent>
              <DropdownMenuItem onClick={() => setEntityDialogOpen(true)}>
                Edit Details
              </DropdownMenuItem>
              <DropdownMenuItem>Manage Trustees</DropdownMenuItem>
              <DropdownMenuItem>Manage Beneficiaries</DropdownMenuItem>
            </DropdownMenuContent>
          </DropdownMenu>
        </div>
      </div>

      {/* Quick Info */}
      <div className="p-3">
        <div className="grid grid-cols-2 gap-2 text-sm">
          <div>
            <p className="text-xs text-gray-500">Jurisdiction</p>
            <p className="truncate font-medium">{data.jurisdiction}</p>
          </div>
          <div>
            <p className="text-xs text-gray-500">Beneficiaries</p>
            <p className="truncate font-medium">
              {data.beneficiaries.length} people
            </p>
          </div>
        </div>
      </div>

      {/* Expandable Section */}
      <div
        className={`overflow-hidden transition-all duration-200 ease-in-out
          ${isExpanded ? 'max-h-48' : 'max-h-0'}
        `}
      >
        <div className="border-t p-3">
          <div className="space-y-2">
            <div>
              <p className="text-xs text-gray-500">Establishment Date</p>
              <p className="font-medium">
                {new Date(data.establishmentDate).toLocaleDateString()}
              </p>
            </div>
            <div>
              <p className="text-xs text-gray-500">Trustees</p>
              <div className="mt-1 flex flex-wrap gap-1">
                {data.trustees.map((trusteeId) => (
                  <span
                    key={trusteeId}
                    className="rounded-full bg-purple-100 px-2 py-0.5 text-xs font-medium text-purple-700"
                  >
                    {trusteeId}
                  </span>
                ))}
              </div>
            </div>
          </div>
        </div>
      </div>

      {/* Expand Toggle */}
      <button
        className="absolute -bottom-3 left-1/2 flex h-6 w-6 -translate-x-1/2 transform items-center justify-center rounded-full border bg-white shadow-sm hover:bg-gray-50"
        onClick={() => setIsExpanded(!isExpanded)}
      >
        <ChevronDown
          className={`h-4 w-4 transition-transform ${
            isExpanded ? 'rotate-180' : ''
          }`}
        />
      </button>

      {/* Quick Actions on Hover */}
      <div className="absolute -right-2 top-1/2 -translate-y-1/2 transform opacity-0 transition-opacity group-hover:opacity-100">
        <div className="flex flex-col gap-1">
          <button className="rounded-full bg-white p-2 shadow-md hover:bg-gray-50">
            <Users className="h-4 w-4" />
          </button>
        </div>
      </div>
    </div>
  )
}

export default memo(TrustNode)
