"use client"

import React, { memo } from 'react'
import { Handle, Position, NodeProps } from 'reactflow'
import { Building2, ChevronDown, MoreHorizontal } from 'lucide-react'
import { CompanyEntity } from '../../types/entities'
import {
  DropdownMenu,
  DropdownMenuContent,
  DropdownMenuItem,
  DropdownMenuTrigger,
} from '../ui/dropdown-menu'
import useEntityStore from '../../stores/entityStore'
import useUIStore from '../../stores/uiStore'

const CompanyNode = ({ data, selected }: NodeProps<CompanyEntity>) => {
  const updateEntity = useEntityStore((state) => state.updateEntity)
  const setEntityDialogOpen = useUIStore((state) => state.setEntityDialogOpen)
  const [isExpanded, setIsExpanded] = React.useState(false)

  const getStatusColor = (status: CompanyEntity['complianceStatus']) => {
    switch (status) {
      case 'Compliant':
        return 'bg-green-500'
      case 'Warning':
        return 'bg-yellow-500'
      case 'Non-compliant':
        return 'bg-red-500'
      default:
        return 'bg-gray-500'
    }
  }

  return (
    <div
      className={`group relative rounded-lg bg-white shadow-lg transition-all
        ${selected ? 'ring-2 ring-blue-500' : ''}
        ${isExpanded ? 'w-80' : 'w-60'}
      `}
    >
      {/* Connection Handles */}
      <Handle
        type="target"
        position={Position.Top}
        className="!bg-gray-400 hover:!bg-blue-500"
      />
      <Handle
        type="source"
        position={Position.Bottom}
        className="!bg-gray-400 hover:!bg-blue-500"
      />
      <Handle
        type="target"
        position={Position.Left}
        className="!bg-gray-400 hover:!bg-blue-500"
      />
      <Handle
        type="source"
        position={Position.Right}
        className="!bg-gray-400 hover:!bg-blue-500"
      />

      {/* Header */}
      <div className="flex items-center justify-between border-b p-3">
        <div className="flex items-center gap-2">
          <div className="rounded-lg bg-blue-50 p-2">
            <Building2 className="h-5 w-5 text-blue-500" />
          </div>
          <div>
            <h3 className="font-medium">{data.name}</h3>
            <p className="text-xs text-gray-500">{data.companyType}</p>
          </div>
        </div>
        <div className="flex items-center gap-1">
          <div
            className={`h-3 w-3 rounded-full ${getStatusColor(
              data.complianceStatus
            )}`}
          />
          <DropdownMenu>
            <DropdownMenuTrigger asChild>
              <button className="rounded p-1 hover:bg-gray-100">
                <MoreHorizontal className="h-4 w-4" />
              </button>
            </DropdownMenuTrigger>
            <DropdownMenuContent>
              <DropdownMenuItem onClick={() => setEntityDialogOpen(true)}>
                Edit Details
              </DropdownMenuItem>
              <DropdownMenuItem
                onClick={() =>
                  updateEntity(data.id, {
                    complianceStatus:
                      data.complianceStatus === 'Compliant'
                        ? 'Non-compliant'
                        : 'Compliant',
                  })
                }
              >
                Toggle Compliance
              </DropdownMenuItem>
            </DropdownMenuContent>
          </DropdownMenu>
        </div>
      </div>

      {/* Quick Info */}
      <div className="p-3">
        <div className="grid grid-cols-2 gap-2 text-sm">
          <div>
            <p className="text-xs text-gray-500">Registration</p>
            <p className="truncate font-medium">{data.registrationNumber}</p>
          </div>
          <div>
            <p className="text-xs text-gray-500">Jurisdiction</p>
            <p className="truncate font-medium">{data.jurisdiction}</p>
          </div>
        </div>
      </div>

      {/* Expandable Section */}
      <div
        className={`overflow-hidden transition-all duration-200 ease-in-out
          ${isExpanded ? 'max-h-48' : 'max-h-0'}
        `}
      >
        <div className="border-t p-3">
          <div className="space-y-2">
            <div>
              <p className="text-xs text-gray-500">Status</p>
              <p className="font-medium">{data.status}</p>
            </div>
            <div>
              <p className="text-xs text-gray-500">Incorporation Date</p>
              <p className="font-medium">
                {new Date(data.incorporationDate).toLocaleDateString()}
              </p>
            </div>
            <div>
              <p className="text-xs text-gray-500">Documents</p>
              <p className="font-medium">{data.documents.length} files</p>
            </div>
          </div>
        </div>
      </div>

      {/* Expand Toggle */}
      <button
        className="absolute -bottom-3 left-1/2 flex h-6 w-6 -translate-x-1/2 transform items-center justify-center rounded-full border bg-white shadow-sm hover:bg-gray-50"
        onClick={() => setIsExpanded(!isExpanded)}
      >
        <ChevronDown
          className={`h-4 w-4 transition-transform ${
            isExpanded ? 'rotate-180' : ''
          }`}
        />
      </button>

      {/* Quick Actions on Hover */}
      <div className="absolute -right-2 top-1/2 -translate-y-1/2 transform opacity-0 transition-opacity group-hover:opacity-100">
        <div className="flex flex-col gap-1">
          <button className="rounded-full bg-white p-2 shadow-md hover:bg-gray-50">
            <Building2 className="h-4 w-4" />
          </button>
        </div>
      </div>
    </div>
  )
}

export default memo(CompanyNode)
