"use client"

import React, { memo } from 'react'
import { Handle, Position, NodeProps } from 'reactflow'
import { User, ChevronDown, MoreHorizontal, Briefcase } from 'lucide-react'
import { IndividualEntity } from '../../types/entities'
import {
  DropdownMenu,
  DropdownMenuContent,
  DropdownMenuItem,
  DropdownMenuTrigger,
} from '../ui/dropdown-menu'
import useUIStore from '../../stores/uiStore'

const IndividualNode = ({ data, selected }: NodeProps<IndividualEntity>) => {
  const setEntityDialogOpen = useUIStore((state) => state.setEntityDialogOpen)
  const [isExpanded, setIsExpanded] = React.useState(false)

  // Generate initials from name
  const initials = data.name
    .split(' ')
    .map((n) => n[0])
    .join('')
    .toUpperCase()
    .slice(0, 2)

  return (
    <div
      className={`group relative transition-all
        ${selected ? 'ring-2 ring-green-500' : ''}
        ${isExpanded ? 'w-72' : 'w-48'}
      `}
    >
      {/* Connection Handles */}
      <Handle
        type="target"
        position={Position.Top}
        className="!bg-green-400 hover:!bg-green-500"
      />
      <Handle
        type="source"
        position={Position.Bottom}
        className="!bg-green-400 hover:!bg-green-500"
      />
      <Handle
        type="target"
        position={Position.Left}
        className="!bg-green-400 hover:!bg-green-500"
      />
      <Handle
        type="source"
        position={Position.Right}
        className="!bg-green-400 hover:!bg-green-500"
      />

      <div className="rounded-lg bg-white shadow-lg">
        {/* Header with Avatar */}
        <div className="flex flex-col items-center p-3">
          <div className="relative">
            <div className="flex h-16 w-16 items-center justify-center rounded-full bg-gradient-to-br from-green-50 to-green-100 text-xl font-semibold text-green-700">
              {initials}
            </div>
            <DropdownMenu>
              <DropdownMenuTrigger asChild>
                <button className="absolute -right-1 -top-1 rounded-full bg-white p-1 shadow-md hover:bg-gray-50">
                  <MoreHorizontal className="h-4 w-4" />
                </button>
              </DropdownMenuTrigger>
              <DropdownMenuContent>
                <DropdownMenuItem onClick={() => setEntityDialogOpen(true)}>
                  Edit Details
                </DropdownMenuItem>
                <DropdownMenuItem>Manage Roles</DropdownMenuItem>
                <DropdownMenuItem>View Shareholdings</DropdownMenuItem>
              </DropdownMenuContent>
            </DropdownMenu>
          </div>
          <h3 className="mt-2 text-center font-medium">{data.name}</h3>
          <p className="text-xs text-gray-500">{data.nationality}</p>
        </div>

        {/* Roles */}
        <div className="flex flex-wrap justify-center gap-1 px-3 pb-3">
          {data.role.map((role) => (
            <span
              key={role}
              className="flex items-center rounded-full bg-green-100 px-2 py-0.5 text-xs font-medium text-green-700"
            >
              <Briefcase className="mr-1 h-3 w-3" />
              {role}
            </span>
          ))}
        </div>

        {/* Expandable Section */}
        <div
          className={`overflow-hidden transition-all duration-200 ease-in-out
            ${isExpanded ? 'max-h-48' : 'max-h-0'}
          `}
        >
          <div className="border-t p-3">
            <div className="space-y-2">
              <div>
                <p className="text-xs text-gray-500">Email</p>
                <p className="truncate font-medium">{data.email}</p>
              </div>
              <div>
                <p className="text-xs text-gray-500">Shareholdings</p>
                <div className="mt-1 space-y-1">
                  {data.shareholdings.map((holding) => (
                    <div
                      key={holding.entityId}
                      className="flex items-center justify-between rounded bg-gray-50 px-2 py-1 text-xs"
                    >
                      <span className="truncate">{holding.entityId}</span>
                      <span className="font-medium">{holding.percentage}%</span>
                    </div>
                  ))}
                </div>
              </div>
            </div>
          </div>
        </div>

        {/* Expand Toggle */}
        <button
          className="absolute -bottom-3 left-1/2 flex h-6 w-6 -translate-x-1/2 transform items-center justify-center rounded-full border bg-white shadow-sm hover:bg-gray-50"
          onClick={() => setIsExpanded(!isExpanded)}
        >
          <ChevronDown
            className={`h-4 w-4 transition-transform ${
              isExpanded ? 'rotate-180' : ''
            }`}
          />
        </button>
      </div>

      {/* Quick Actions on Hover */}
      <div className="absolute -right-2 top-1/2 -translate-y-1/2 transform opacity-0 transition-opacity group-hover:opacity-100">
        <div className="flex flex-col gap-1">
          <button className="rounded-full bg-white p-2 shadow-md hover:bg-gray-50">
            <User className="h-4 w-4" />
          </button>
        </div>
      </div>
    </div>
  )
}

export default memo(IndividualNode)
